
html, body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Inter', sans-serif; }

ul {
    list-style: none;
    padding-left: 0; }

a {
    text-decoration: none;
    color: #fff; }

h1,h2,h3,h4,h5,h6,p {
    margin: 0; }

button {
    background: transparent;
    border: none; }

body {
        background: #1f1f24; }

.nav {
    padding: 7px 0;
    position: fixed;
    background: #1f1f24;
    width: 100%;
    z-index: 9;
    &-container {
        max-width: 1200px;
        margin: 0 auto;
        display: flex;
        &-banner {
            line-height: 56px; }
        &-menu {
            margin-left: auto;
            display: flex;
            &-item {
                padding: 0 9px;
                &-link {
                    padding: 22px 6px; } } } } }

.header {
    max-width: 1200px;
    display: block;
    margin: 0 auto;
    padding-top: 80px;
    padding-bottom: 60px;
    position: relative;
    &-hero {
        padding-top: 90px;
        &-container {
            text-align: center;
            h1 {
                font-size: 76px;
                color: #fff;
                padding-bottom: 12px;
                @media only screen and (max-width: 768px) {
                    font-size: 56px; } }
            p {
                font-size: 24px;
                color: #fff;
                max-width: 650px;
                margin: 0 auto 60px auto;
                padding: 0 30px;
                @media only screen and (max-width: 768px) {
                    font-size: 18px; } }
            &-buttons {
                &-button {
                    border-radius: 8px;
                    color: #fff;
                    border: 2px solid transparent;
                    text-transform: capitalize;
                    transition: border-color .1s ease-out,background-color .1s ease-out,color .1s ease-out;
                    min-width: 172px;
                    padding: 14px;
                    font-size: 16px;
                    margin: 0 12px;
                    &:nth-child(1) {
                        background: #FFA200;
                        &:hover {
                            background: #F9CE84;
                            border-color: #F9CE84; } }
                    &:nth-child(2) {
                        border: 2px solid #f8fafd;
                        color: #f8fafd;
                        &:hover {
                            color: #1f1f24;
                            background: #fff;
                            border-color: #fff; } } } } }
        &-image-container {
            margin-top: 80px;
            max-width: 1280px;
            padding: 0 30px;
            &-image {
                width: 100%;
                box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
                border-radius: 8px; } } } }

.main {
    background: #fff;
    padding-bottom: 120px;
    &-about {
        margin-top: 80px;
        padding: 0 30px;
        &-container {
            margin: 0 auto;
            display: table;
            text-align: center;
            color: #fff;
            p {
                max-width: 960px;
                font-size: 24px;
                font-weight: 600; } } }
    &-features {
        max-width: 960px;
        padding: 0 30px;
        margin: 60px auto 0 auto;
        &-feature {
            @media only screen and (max-width: 768px) {
                display: block;
                height: 100%; }
            display: flex;
            background: #f9fafb;
            border-radius: 8px;
            transition: box-shadow 0.3s ease-in-out;
            margin-bottom: 30px;
            height: 300px;
            &:hover {
                box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22); }
            &:nth-child(even) {
                flex-direction: row-reverse; }
            &:nth-child(even) &-content {
                margin-right: unset;
                margin-left: auto; }
            &:nth-child(even) &-image img {
                border-top-right-radius: 0;
                border-bottom-right-radius: 0;
                border-top-left-radius: 8px;
                border-bottom-left-radius: 8px; }
            &-content {
                padding: 30px;
                max-width: 450px;
                margin-right: auto;
                h3 {
                    margin-bottom: 5px;
                    color: #000; }
                p {
                    margin-bottom: 20px;
                    color: #000; }
                a {
                    color: #FFA200; } }
            &-image {
                img {
                    border-top-right-radius: 8px;
                    border-bottom-right-radius: 8px;
                    height: 100%;
                    max-height: 300px; } } } }
    &-trynow {
        margin: 80px auto 0 auto;
        max-width: 960px;
        &-container {
            background: #FFA200;
            padding: 15px 30px;
            border-radius: 8px;
            text-align: center;
            h1 {
                color: #fff;
                font-size: 36px; }
            &-buttons {
                margin-top: 20px;
                &-button {
                    border-radius: 8px;
                    border: 2px solid #f8fafd;
                    color: #f8fafd;
                    text-transform: capitalize;
                    transition: border-color .1s ease-out,background-color .1s ease-out,color .1s ease-out;
                    min-width: 172px;
                    padding: 14px;
                    font-size: 12px;
                    margin: 0 12px;
                    &:nth-child(1) {
                        background: #fff;
                        color: #FFA200; } } } } } }
.footer {
    margin-top: -20px;
    background: #fff;
    &-container {
        max-width: 960px;
        margin: 0 auto;
        &-shin {
            background-image: url('https://element.io/images/collage-012x.jpg');
            background-position: 50% 100%;
            background-size: 1181px;
            background-repeat: no-repeat;
            min-height: 750px;
            margin-bottom: 40px;
            &-content {
                padding-top: 40px;
                text-align: center;
                max-width: 650px;
                margin: 0 auto;
                h2 {
                    font-size: 36px; }
                button {
                    margin-top: 40px;
                    border-radius: 8px;
                    border: 2px solid #f8fafd;
                    color: #f8fafd;
                    text-transform: capitalize;
                    transition: border-color .1s ease-out,background-color .1s ease-out,color .1s ease-out;
                    min-width: 172px;
                    padding: 14px;
                    font-size: 16px;
                    background: #FFA200; }
                p {
                    margin-top: 10px; } } }
        &-links {
            padding-top: 40px;
            border-top: 1px solid #e9ecf1;
            border-bottom: 1px solid #e9ecf1;
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
            width: 100%;
            @media screen and (max-width: 960px) {
                grid-template-columns: 1fr;
                ul {
                    margin: 20px auto; } }
            ul {
                font-size: 20px;
                li {
                    &:nth-child(1) {
                        font-weight: bold;
                        margin-bottom: 1em; }
                    margin-bottom: 0.7em; } } }
        &-copyright {
            padding-top: 20px;
            padding-bottom: 40px;
            display: flex;
            p {
                margin-right: auto; } } } }
